/**
Global Editor CSS for Resource Blocks

CSS code that gets applied to the editor.

Build file is named: global.css
 */

html :where(.wp-block) {
  max-width: initial;
  margin-top: 0;
  margin-bottom: 0;
}

html :where(.editor-styles-wrapper) {
  padding: 35px;
}

.is-root-container.block-editor-block-list__layout {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
}

div.resource-blocks-row.wp-block.wp-block-resource-blocks-two-column-images {
  // disable the 'row'
  margin: 0;
  display: block;
  padding-left: 15px;
  padding-right: 15px;

  .block-editor-block-list__layout {
	// new 'Row'
	display: flex;
	flex-wrap: wrap;
	margin-right: -35px;
	margin-left: -35px;
  }
}
